<template>
  <div class="body">
    <el-descriptions>
      <el-descriptions-item label="收货地址">
        <el-input type="text" v-model="userInfo.userName" placeholder="请输入姓名"/>
        <el-input type="text" v-model="userInfo.phone" placeholder="请输入手机号"/>
        <el-input type="textarea" v-model="userInfo.address" placeholder="请输入收货地址"/>
        <el-button size="mini" type="primary" plain style="margin: 10px 0" @click="updateUserInfo">保 存</el-button>
      </el-descriptions-item>
    </el-descriptions>
  </div>
</template>
<script>
  export default {
    computed: {
      userInfo(){
        return this.$store.getters.userInfo
      }
    },
    methods:{
      updateUserInfo(){
        this.$request.doPost('/api/login/updateUserInfo',this.userInfo).then(res => {
          if (res.isOk) {
            this.showSuccess("修改成功");
          }
        });
      }
    }
  };
</script>
<style scoped>
</style>





