<!-- 【通知信息】弹窗页面 -->
<template>
  <div>
    <user-login ref="userLoginDialog"></user-login>
    <el-tabs class="client" v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="商品列表" name="goodsList">
        <goodsList ref="goodsList"></goodsList>
      </el-tab-pane>
      <el-tab-pane label="购物车" name="shopCartList">
        <shopCartList ref="shopCartList"></shopCartList>
      </el-tab-pane>
      <el-tab-pane label="我的订单" name="ordersList">
        <ordersList ref="ordersList"></ordersList>
      </el-tab-pane>
      <el-tab-pane label="个人中心" name="userCenter">
        <userCenter ref="userCenter"></userCenter>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  import userLogin from './personal/userLogin'
  import userCenter from './../service/userCenter/userCenter'
  import goodsList from './../service/goods/goodsList'
  import ordersList from './../service/orders/ordersList'
  import shopCartList from './../service/shopCart/shopCartList'
  export default {
    components: {userLogin,userCenter,goodsList,ordersList,shopCartList},
    data() {
      return {
        activeName: 'goodsList'
      };
    },
    methods: {
      handleClick(tab, event) {
        if (this.activeName == 'homeService') {
          this.$refs.homeService.getList()
        } else if (this.activeName == 'shopCartList') {
          this.$refs.shopCartList.getList()
        } else if (this.activeName == 'ordersList') {
          this.$refs.ordersList.getList()
        } else if (this.activeName == 'userCenter') {
          this.$refs.userCenter.getList()
        }
      },
    }
  };
</script>
<style scoped>
  .client{
    background:#EFEFEF;
  }
</style>





