<template>
  <div class="body">
    <el-descriptions title="基础信息">
      <el-descriptions-item label="头像"><el-image style="height: 100px;border-radius: 50%" :src="formatUrl(userInfo.photo)"/></el-descriptions-item>
    </el-descriptions>
    <el-descriptions>
      <el-descriptions-item label="登录名"><el-tag size="small">{{userInfo.loginName}}</el-tag></el-descriptions-item>
      <el-descriptions-item label="手机号">{{userInfo.phone}}</el-descriptions-item>
      <el-descriptions-item label="邮箱">{{userInfo.email}}</el-descriptions-item>
    </el-descriptions>
  </div>

</template>
<script>
  export default {
    computed: {
      userInfo(){
        return this.$store.getters.userInfo
      }
    },
  };
</script>
<style scoped>
</style>





