<!-- 【注册信息】弹窗页面 -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="userRegister" ref="userRegisterForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="主键id" class="hidden" prop="id">
          <el-input type="hidden" v-model="userRegister.id"/>
        </el-form-item>
        <el-form-item label="头像" prop="headPicture">
          <sui-photo type="userRegister_headPicture" v-model="userRegister.headPicture" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="登录名" prop="loginName">
          <el-input v-model="userRegister.loginName" placeholder="请输入登录名" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="userRegister.password" placeholder="请输入密码" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="姓名" prop="realName">
          <el-input v-model="userRegister.realName" placeholder="请输入姓名" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="工号" prop="userNo">
          <el-input v-model="userRegister.userNo" placeholder="请输入工号" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="用户类型" prop="userType">
          <el-select v-model="userRegister.userType" placeholder="请选择用户类型" :disabled="dialog.disabled" >
            <el-option v-for="dict in userTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="手机号" prop="phone">
          <el-input v-model="userRegister.phone" placeholder="请输入手机号" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="身份证号" prop="idCard">
          <el-input v-model="userRegister.idCard" placeholder="请输入身份证号" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="userRegister.email" placeholder="请输入邮箱" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="详细地址" prop="address">
          <el-input type="textarea" v-model="userRegister.address" placeholder="请输入详细地址" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="授权表id" prop="userAuthorId">
          <el-input v-model="userRegister.userAuthorId" placeholder="请输入授权表id" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel">取 消</el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "userRegisterForm",
    data() {
      return {
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        userRegister: {
          headPicture: "",
          loginName: "",
          password: "",
          realName: "",
          userNo: "",
          userType: "",
          phone: "",
          idCard: "",
          email: "",
          address: "",
          userAuthorId: ""
        },
        //表单校验
        rules: {
          headPicture: [{required: true, message: '请选择头像', trigger: 'change'}],
          loginName: [{required: true, message: '请输入登录名', trigger: 'blur'}],
          password: [{required: true, message: '请输入密码', trigger: 'blur'}],
          realName: [{required: true, message: '请输入姓名', trigger: 'blur'}],
          userNo: [{required: true, message: '请输入工号', trigger: 'blur'}],
          userType: [{required: true, message: '请选择用户类型', trigger: 'change'}],
          phone: [{required: true, message: '请输入手机号', trigger: 'blur'}],
          idCard: [{required: true, message: '请输入身份证号', trigger: 'blur'}],
          email: [{required: true, message: '请输入邮箱', trigger: 'blur'}],
          address: [{required: true, message: '请输入详细地址', trigger: 'blur'}],
          userAuthorId: [{required: true, message: '请输入授权表id', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //【用户类型】
      userTypeDict() {
        return this.$store.getters.getDictArray('user_type');
      },
    },
    methods: {
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getUserRegister(option.id);
        }
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false;
      },
      //根据id获取数据
      getUserRegister(id) {
        this.$request.doGet("/admin/userRegister/get?id=" + id).then(res => {
          if (res.isOk) {
            this.userRegister = res.obj;
          }
        });
      },
      //提交表单
      submitForm: function () {
        const that = this;
        that.$refs.userRegisterForm.validate((valid) => {
          if (valid) {//表单校验
            that.$request.doPost('/admin/userRegister/sub', that.userRegister).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.userRegisterForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>

</style>



