<!-- 【文件信息】弹窗页面 -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="files" ref="filesForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="主键id" class="hidden" prop="id">
          <el-input type="hidden" v-model="files.id"/>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="files.name" placeholder="请输入名称" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="类型" prop="type">
          <el-input v-model="files.type" placeholder="请输入类型" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="路径" prop="path">
          <el-input v-model="files.path" placeholder="请输入路径" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="星级" prop="stars">
          <el-input v-model="files.stars" placeholder="请输入星级" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="备注" prop="remarks">
          <el-input v-model="files.remarks" placeholder="请输入备注" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="父级" prop="pid">
          <el-input v-model="files.pid" placeholder="请输入父级" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel">取 消</el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "filesForm",
    data() {
      return {
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        files: {
          id: "",
          name: "",
          type: "",
          path: "",
          stars: "",
          remarks: "",
          pid: ""
        },
        //表单校验
        rules: {
          name: [{required: true, message: '请输入名称', trigger: 'blur'}],
          type: [{required: true, message: '请输入类型', trigger: 'blur'}],
          path: [{required: true, message: '请输入路径', trigger: 'blur'}],
          stars: [{required: true, message: '请输入星级', trigger: 'blur'}],
          remarks: [{required: true, message: '请输入备注', trigger: 'blur'}],
          pid: [{required: true, message: '请输入父级', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getFiles(option.id);
        }
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false;
      },
      //根据id获取数据
      getFiles(id) {
        this.$request.doGet("/admin/files/get?id=" + id).then(res => {
          if (res.isOk) {
            this.files = res.obj;
          }
        });
      },
      //提交表单
      submitForm: function () {
        const that = this;
        that.$refs.filesForm.validate((valid) => {
          if (valid) {//表单校验
            that.$request.doPost('/admin/files/sub', that.files).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.filesForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>

</style>



