<!-- 【字典信息】弹窗页面 -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="sysDict" ref="sysDictForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="主键id" class="hidden" prop="id">
          <el-input type="hidden" v-model="sysDict.id"/>
        </el-form-item>
        <el-form-item label="父级编号" prop="pid">
          <el-input v-model="sysDict.pid" placeholder="请输入父级编号" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="标签名" prop="label">
          <el-input v-model="sysDict.label" placeholder="请输入标签名" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="数据值" prop="value">
          <el-input v-model="sysDict.value" placeholder="请输入数据值" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="类型" prop="type">
          <el-input v-model="sysDict.type" placeholder="请输入类型" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="样式" prop="style">
          <el-input v-model="sysDict.style" placeholder="请输入样式" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="排序" prop="sort">
          <el-input v-model="sysDict.sort" placeholder="请输入排序" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="备注信息" prop="remarks">
          <el-input v-model="sysDict.remarks" placeholder="请输入备注信息" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel">取 消</el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "sysDictForm",
    data() {
      return {
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        sysDict: {
          pid: "",
          label: "",
          value: "",
          type: "",
          style: "",
          sort: "",
          remarks: ""
        },
        //表单校验
        rules: {
          pid: [{required: true, message: '请输入父级编号', trigger: 'blur'}],
          label: [{required: true, message: '请输入标签名', trigger: 'blur'}],
          value: [{required: true, message: '请输入数据值', trigger: 'blur'}],
          type: [{required: true, message: '请输入类型', trigger: 'blur'}],
          style: [{required: true, message: '请输入样式', trigger: 'blur'}],
          sort: [{required: true, message: '请输入排序', trigger: 'blur'}],
          remarks: [{required: true, message: '请输入备注信息', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSysDict(option.id);
        }
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false;
      },
      //根据id获取数据
      getSysDict(id) {
        this.$request.doGet("/admin/sysDict/get?id=" + id).then(res => {
          if (res.isOk) {
            this.sysDict = res.obj;
          }
        });
      },
      //提交表单
      submitForm: function () {
        const that = this;
        that.$refs.sysDictForm.validate((valid) => {
          if (valid) {//表单校验
            that.$request.doPost('/admin/sysDict/sub', that.sysDict).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysDictForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>

</style>



