<!-- 【商品信息】弹窗页面 -->
<template>
  <div class="body">
    <pay-order ref="payOrder" @reloadList="reloadList"></pay-order>
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="goods" ref="goodsForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="主键id" class="hidden" prop="id">
          <el-input type="hidden" v-model="goods.id"/>
        </el-form-item>
        <el-form-item label="主图" prop="picture">
          <sui-photo type="goods_picture" v-model="goods.picture" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="标题" prop="title">
          <el-input v-model="goods.title" placeholder="请输入标题" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="goods.name" placeholder="请输入名称" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="类型" prop="type">
          <el-select v-model="goods.type" placeholder="请选择类型" :disabled="dialog.disabled" >
            <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="价格" prop="price">
          <el-input v-model="goods.price" placeholder="请输入价格" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="颜色" prop="color">
          <el-input v-model="goods.color" placeholder="请输入颜色" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="规格" prop="specs">
          <el-input v-model="goods.specs" placeholder="请输入规格" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="描述" prop="remarks">
          <el-input type="textarea" v-model="goods.remarks" placeholder="请输入描述" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="文件列表" prop="fileList">
          <sui-file type="goods" :refId="goods.id" v-model="goods.fileList" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-tabs type="border-card">
          <el-tab-pane label="商品详情">商品详情</el-tab-pane>
          <el-tab-pane label="累计评价">累计评价</el-tab-pane>
        </el-tabs>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <label>数量:</label><el-input-number size="small" :min=1 :max=100 v-model="selectAmount" :step="1"></el-input-number>
        <label>合计:</label><span style="color: red;margin-right: 30px">{{totalPrice}} 元</span>
        <el-button size="small" type="danger" style="color: #E5511D;border-color: #F0CAB6;background: #FFE4D0;" @click="addOrder()">立即购买</el-button>
        <el-button size="small" type="danger" style="color: #FFF;border-color: #F40;background: #F40;" @click="addShopCart()"><i class="fa fa-shopping-cart" style="margin-right: 5px"></i>加入购物车</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import payOrder from '../payOrder/payOrder'
  export default {
    components:{payOrder},
    name: "goodsForm",
    data() {
      return {
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        selectAmount:1,
        //表单数据
        goods: {
          id: "",
          picture: "",
          title: "",
          name: "",
          type: "",
          price: "",
          color: "",
          specs: "",
          remarks: ""
        },
        shopCart: {
          id: "",
          name: "",
          userId: "",
          price: "",
          amount: "",
          orderTime: "",
          goodsId: ""
        },
        orders: {
          id: "",
          picture: "",
          title: "",
          name: "",
          orderTime: "",
          goodsId: "",
          color: "",
          price: "",
          amount: "",
          specs: "",
          orderStatus: "",
          userPhone: "",
          userId: "",
          userName: "",
          userAddr: "",
          remarks: ""
        },
      }
    },
    computed: {
      //【类型】
      typeDict() {
        return this.$store.getters.getDictArray('goods_type');
      },
      hasLogin() {
        return this.$store.getters.hasLogin;
      },
      totalPrice() {
        return parseInt(this.selectAmount)*parseInt(this.goods.price);
      },
    },
    watch: {
      goods(value){
        this.selectAmount=1
      }
    },
    methods: {
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getGoods(option.id);
        }
      },
      reloadList(){
        this.cancel()
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false;
      },
      //根据id获取数据
      getGoods(id) {
        this.$request.doGet("/api/goods/getGoods?id=" + id).then(res => {
          if (res.isOk) {
            this.goods = res.obj;
          }
        });
      },
      //购买
      addOrder(){
        if(!this.hasLogin){
         this.showWarn("请先进行登录");
          return
        }
        this.orders.goodsId= this.goods.id;
        this.orders.amount = this.selectAmount;
        this.orders.price= this.goods.price;
        this.orders.title= this.goods.title;
        this.orders.name= this.goods.name;
        this.orders.color= this.goods.color;
        this.orders.specs= this.goods.specs;
        this.orders.picture= this.goods.picture;
        this.$request.doPost('/api/orders/sub', this.orders).then(res => {
          if (res.isOk) {//需要拿到订单id
            let orders = Object.assign({}, res.obj)
            orders.totalPrice = this.totalPrice
            this.$refs.payOrder.open({title:'订单支付',type:'buy',orders:orders});
          }
        });
      },
      //加入购物车
      addShopCart(){
        if(!this.hasLogin){
          this.showWarn("请先进行登录");
          return
        }
        this.shopCart.goodsId = this.goods.id;
        this.shopCart.amount = this.selectAmount;
        this.shopCart.totalPrice = this.totalPrice;
        this.shopCart.price = this.goods.price;
        this.shopCart.title = this.goods.title;
        this.shopCart.name = this.goods.name;
        this.shopCart.color= this.goods.color;
        this.shopCart.specs= this.goods.specs;
        this.shopCart.picture= this.goods.picture;

        this.$request.doPost('/api/shopCart/sub', this.shopCart).then(res => {
          if (res.isOk) {
            this.cancel();
            this.showSuccess("购物添加成功，继续购物吧。≧◉◡◉≦");
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.goodsForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>

</style>



