<!-- 【订单信息】管理页面 -->
<template>
  <div class="body">
    <!-- 表单弹窗 -->
    <orders-form ref="ordersDialog" @reloadList="getList"/>
    <!-- 查询条件 -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="标题" prop="title">
        <el-input v-model="queryForm.title" placeholder="请输入标题" size="small" clearable/>
      </el-form-item>
      <el-form-item label="名称" prop="name">
        <el-input v-model="queryForm.name" placeholder="请输入名称" size="small" clearable/>
      </el-form-item>
      <el-form-item label="下单时间" prop="orderTime">
        <el-date-picker v-model="queryForm.orderTime" placeholder="请选择下单时间" type="date" value-format="yyyy-MM-dd" size="small" clearable/>
      </el-form-item>
      <el-form-item label="所属商品" prop="goodsId">
        <el-select v-model="queryForm.goodsId" placeholder="请选择所属商品" size="small" clearable>
          <el-option v-for="goods in goodsDict" :key="goods.id" :value="goods.id" :label="goods.name"/>
        </el-select>
      </el-form-item>
      <el-form-item label="颜色" prop="color">
        <el-input v-model="queryForm.color" placeholder="请输入颜色" size="small" clearable/>
      </el-form-item>
      <el-form-item label="价格" prop="price">
        <el-input v-model="queryForm.price" placeholder="请输入价格" size="small" clearable/>
      </el-form-item>
      <el-form-item label="数量" prop="amount">
        <el-input v-model="queryForm.amount" placeholder="请输入数量" size="small" clearable/>
      </el-form-item>
      <el-form-item label="规格" prop="specs">
        <el-input v-model="queryForm.specs" placeholder="请输入规格" size="small" clearable/>
      </el-form-item>
      <el-form-item label="订单状态" prop="orderStatus">
        <el-select v-model="queryForm.orderStatus" placeholder="请选择订单状态" size="small" clearable>
          <el-option v-for="dict in orderStatusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="下单用户" prop="userId">
        <el-select v-model="queryForm.userId" placeholder="请选择下单用户" size="small" clearable>
          <el-option v-for="sysUser in sysUserDict" :key="sysUser.id" :value="sysUser.id" :label="sysUser.loginName"/>
        </el-select>
      </el-form-item>
      <el-form-item label="手机号" prop="userPhone">
        <el-input v-model="queryForm.userPhone" placeholder="请输入手机号" size="small" clearable/>
      </el-form-item>
      <el-form-item label="联系人" prop="userName">
        <el-input v-model="queryForm.userName" placeholder="请输入联系人" size="small" clearable/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()">重置</el-button>
      </el-form-item>
    </el-form>
    <!-- 查询按钮 -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:'新增订单信息'})" v-hasPermi="['orders:add']">新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['orders:delete']">批量删除</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-export type="primary" size="mini" action="/admin/orders/export" title="导出数据" v-hasPermi="['orders:export']"/>
      </el-col>
      <el-col :span="1.5">
        <el-import type="primary" size="mini" action="/admin/orders/import" title="导入数据" v-hasPermi="['orders:import']"/>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!-- 表格区域 -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="序号" type="index" fixed width="50"/>
      <el-table-column label="主图" prop="picture" sortable>
        <template slot-scope="scope">
          <el-image :src="formatUrl(scope.row.picture)" class="column-image"></el-image>
        </template>
      </el-table-column>
      <el-table-column label="标题" prop="title" sortable/>
      <el-table-column label="名称" prop="name" sortable/>
      <el-table-column label="所属商品" prop="goodsId" sortable>
        <template slot-scope="scope">
          <span v-for="goods in goodsDict" v-if="goods.id==scope.row.goodsId">{{goods.name}}</span>
        </template>
      </el-table-column>
      <el-table-column label="颜色" prop="color" sortable/>
      <el-table-column label="价格" prop="price" sortable/>
      <el-table-column label="数量" prop="amount" sortable/>
      <el-table-column label="规格" prop="specs" sortable/>
      <el-table-column label="订单状态" prop="orderStatus" sortable>
        <template slot-scope="scope">
          <span v-for="dict in orderStatusDict" v-if="dict.dictValue==scope.row.orderStatus">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="下单用户" prop="userId" sortable>
        <template slot-scope="scope">
          <span v-for="sysUser in sysUserDict" v-if="sysUser.id==scope.row.userId">{{sysUser.loginName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="手机号" prop="userPhone" sortable/>
      <el-table-column label="联系人" prop="userName" sortable/>
      <el-table-column label="收货地址" prop="userAddr" show-overflow-tooltip sortable/>
      <el-table-column label="备注" prop="remarks" show-overflow-tooltip sortable/>
      <el-table-column label="操作" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'查看订单信息',pageFrom:'view',id:scope.row.id})" v-hasPermi="['orders:view']">查看</el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'修改订单信息',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['orders:edit']">修改</el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['orders:delete']">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页区域 -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import ordersForm from './ordersForm'
    export default {
      components: {ordersForm},
      data() {
        return {
          loading: true,//是否加载完成
          selectedData: [],//勾选数据
          total: 0, //记录数
          goodsDict: [],
          sysUserDict: [],
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            id: "",
            picture: "",
            title: "",
            name: "",
            orderTime: "",
            goodsId: "",
            color: "",
            price: "",
            amount: "",
            specs: "",
            orderStatus: "",
            userId: "",
            userPhone: "",
            userName: "",
            userAddr: "",
            remarks: ""
          }
        };
      },
      mounted() {
        //初始化数据
        this.getList();
        //初始化【所属商品】
        this.getGoodsDict();
        //初始化【下单用户】
        this.getSysUserDict();
      },
      computed: {
        //【订单状态】
        orderStatusDict() {
          return this.$store.getters.getDictArray('order_status');
        },
      },
      methods: {
        //点击搜索
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //查询列表数据
        getList() {
          this.$request.doGet('/admin/orders/getPage', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //【所属商品】
        getGoodsDict() {
          this.$request.doGet('/admin/goods/getList').then(res => {
            if (res.isOk) {
              this.goodsDict = res.list;
            }
          });
        },
        //【下单用户】
        getSysUserDict() {
          this.$request.doGet('/admin/sysUser/getList').then(res => {
            if (res.isOk) {
              this.sysUserDict = res.list;
            }
          });
        },
        //重置查询
        resetQuery() {
          this.$refs['queryForm'].resetFields();
        },
        //打开弹窗
        openDialog(option) {
          this.$refs.ordersDialog.open(option);
        },
        //选择页数
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //选择页码
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //获取勾选数据
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //勾选批量删除
        handleDeleteAll() {
          const that = this;
          that.showConfirm('确定删除选中的除数据吗?', function () {
            if (that.selectedData.length > 0) {
              that.selectedData.forEach(function (item, index) {
                that.delete(item.id);
              });
              that.showSuccess("删除成功");
            } else {
              that.showWarn("至少选择一条数据！");
            }
          });
        },
        //选中行删除
        handleDelete(id) {
          const that = this;
          that.showConfirm('确定删除选中的除数据吗?', function () {
            that.delete(id);
            that.showSuccess("删除成功");
          });
        },
        //删除方法
        delete(id) {
          this.$request.doGet('/admin/orders/delete?id=' + id).then(res => {
            if (res.isOk) {
              this.getList();
            }
          });
        },
      }
    };
</script>



