<!-- 【订单信息】弹窗页面 -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="orders" ref="ordersForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="主键id" class="hidden" prop="id">
          <el-input type="hidden" v-model="orders.id"/>
        </el-form-item>
        <el-form-item label="主图" prop="picture">
          <sui-photo type="orders_picture" v-model="orders.picture" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="标题" prop="title">
          <el-input v-model="orders.title" placeholder="请输入标题" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="orders.name" placeholder="请输入名称" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="下单时间" prop="orderTime">
          <el-date-picker v-model="orders.orderTime" placeholder="请选择下单时间" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
        </el-form-item>
        <el-form-item label="所属商品" prop="goodsId">
          <el-select v-model="orders.goodsId" placeholder="请选择所属商品" :disabled="dialog.disabled">
            <el-option v-for="goods in goodsDict" :key="goods.id" :value="goods.id" :label="goods.name"/>
          </el-select>
        </el-form-item>
        <el-form-item label="颜色" prop="color">
          <el-input v-model="orders.color" placeholder="请输入颜色" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="价格" prop="price">
          <el-input v-model="orders.price" placeholder="请输入价格" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="数量" prop="amount">
          <el-input v-model="orders.amount" placeholder="请输入数量" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="规格" prop="specs">
          <el-input v-model="orders.specs" placeholder="请输入规格" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="订单状态" prop="orderStatus">
          <el-select v-model="orders.orderStatus" placeholder="请选择订单状态" :disabled="dialog.disabled" >
            <el-option v-for="dict in orderStatusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="下单用户" prop="userId">
          <el-select v-model="orders.userId" placeholder="请选择下单用户" :disabled="dialog.disabled">
            <el-option v-for="sysUser in sysUserDict" :key="sysUser.id" :value="sysUser.id" :label="sysUser.loginName"/>
          </el-select>
        </el-form-item>
        <el-form-item label="手机号" prop="userPhone">
          <el-input v-model="orders.userPhone" placeholder="请输入手机号" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="联系人" prop="userName">
          <el-input v-model="orders.userName" placeholder="请输入联系人" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="收货地址" prop="userAddr">
          <el-input type="textarea" v-model="orders.userAddr" placeholder="请输入收货地址" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="备注" prop="remarks">
          <el-input type="textarea" v-model="orders.remarks" placeholder="请输入备注" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel">取 消</el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "ordersForm",
    data() {
      return {
        goodsDict:[],
        sysUserDict:[],
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        orders: {
          id: "",
          picture: "",
          title: "",
          name: "",
          orderTime: "",
          goodsId: "",
          color: "",
          price: "",
          amount: "",
          specs: "",
          orderStatus: "",
          userId: "",
          userPhone: "",
          userName: "",
          userAddr: "",
          remarks: ""
        },
        //表单校验
        rules: {
          picture: [{required: true, message: '请选择主图', trigger: 'change'}],
          title: [{required: true, message: '请输入标题', trigger: 'blur'}],
          name: [{required: true, message: '请输入名称', trigger: 'blur'}],
          orderTime: [{required: true, message: '请选择下单时间', trigger: 'change'}],
          goodsId: [{required: true, message: '请选择所属商品', trigger: 'change'}],
          color: [{required: true, message: '请输入颜色', trigger: 'blur'}],
          price: [{required: true, message: '请输入价格', trigger: 'blur'}],
          amount: [{required: true, message: '请输入数量', trigger: 'blur'}],
          specs: [{required: true, message: '请输入规格', trigger: 'blur'}],
          orderStatus: [{required: true, message: '请选择订单状态', trigger: 'change'}],
          userId: [{required: true, message: '请选择下单用户', trigger: 'change'}],
          userPhone: [{required: true, message: '请输入手机号', trigger: 'blur'}],
          userName: [{required: true, message: '请输入联系人', trigger: 'blur'}],
          userAddr: [{required: true, message: '请输入收货地址', trigger: 'blur'}],
          remarks: [{required: true, message: '请输入备注', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //【订单状态】
      orderStatusDict() {
        return this.$store.getters.getDictArray('order_status');
      },
    },
    methods: {
      //【所属商品】
      getGoodsDict() {
        this.$request.doGet('/admin/goods/getList').then(res => {
          if (res.isOk) {
            this.goodsDict = res.list;
          }
        })
      },
      //【下单用户】
      getSysUserDict() {
        this.$request.doGet('/admin/sysUser/getList').then(res => {
          if (res.isOk) {
            this.sysUserDict = res.list;
          }
        })
      },
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getGoodsDict();//加载【所属商品】
        this.getSysUserDict();//加载【下单用户】
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getOrders(option.id);
        }
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false;
      },
      //根据id获取数据
      getOrders(id) {
        this.$request.doGet("/admin/orders/get?id=" + id).then(res => {
          if (res.isOk) {
            this.orders = res.obj;
          }
        });
      },
      //提交表单
      submitForm: function () {
        const that = this;
        that.$refs.ordersForm.validate((valid) => {
          if (valid) {//表单校验
            that.$request.doPost('/admin/orders/sub', that.orders).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.ordersForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>

</style>



