<!-- 【通知信息】管理页面 -->
<template>
  <div>
    <!--headers个人中心-->
    <personal></personal>
    <!--主页展示-->
    <service></service>
    <el-footer class="copyright">Copyright © 2021 by {{projectName}}</el-footer>
  </div>
</template>
<style scoped>
</style>
<script>
  import personal from '../home/personal'
  import service from '../home/service'
  import {mapActions} from "vuex";
  export default {
    components: {personal,service},
    mounted() {
      //加载配置数据
      this.commitClientLoginInfo()
      this.commitDictList()
      this.commitConfig()
    },
    computed:{
      projectName(){
        return this.$store.getters.config.projectName
      },
    },
    methods:{
      ...mapActions(['commitConfig', 'commitDictList','commitClientLoginInfo']),
    }
  }

</script>
<style scoped>
  .copyright {
    position: absolute;
    bottom: 0;
    width: 100%;
    background: ghostwhite;
    padding: 5px;
    height: 30px!important;
    text-align: center;
    font-size: smaller;
    color: grey;
  }
</style>




