<!-- 【消息信息】管理页面 -->
<template>
  <div class="body">
    <!-- 表单弹窗 -->
    <sysMessage-form ref="sysMessageDialog" @reloadList="getList"/>
    <!-- 查询条件 -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="消息类型" prop="msgType">
        <el-select v-model="queryForm.msgType" placeholder="请选择消息类型" size="small" clearable>
          <el-option v-for="dict in msgTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="父级id" prop="pid">
        <el-input v-model="queryForm.pid" placeholder="请输入父级id" size="small" clearable/>
      </el-form-item>
      <el-form-item label="用户id" prop="userId">
        <el-input v-model="queryForm.userId" placeholder="请输入用户id" size="small" clearable/>
      </el-form-item>
      <el-form-item label="关联id" prop="refId">
        <el-input v-model="queryForm.refId" placeholder="请输入关联id" size="small" clearable/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()">重置</el-button>
      </el-form-item>
    </el-form>
    <!-- 查询按钮 -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:'新增消息信息'})" v-hasPermi="['sysMessage:add']">新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysMessage:delete']">批量删除</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-export type="primary" size="mini" action="/admin/sysMessage/export" title="导出数据" v-hasPermi="['sysMessage:export']"/>
      </el-col>
      <el-col :span="1.5">
        <el-import type="primary" size="mini" action="/admin/sysMessage/import" title="导入数据" v-hasPermi="['sysMessage:import']"/>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!-- 表格区域 -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="序号" type="index" fixed width="50"/>
      <el-table-column label="消息类型" prop="msgType" width="100" sortable>
        <template slot-scope="scope">
          <span v-for="dict in msgTypeDict" v-if="dict.dictValue==scope.row.msgType">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="父级id" prop="pid" sortable/>
      <el-table-column label="用户id" prop="userId" sortable/>
      <el-table-column label="关联id" prop="refId" sortable/>
      <el-table-column label="消息内容" prop="content" show-overflow-tooltip sortable/>
      <el-table-column label="操作" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'查看消息信息',pageFrom:'view',id:scope.row.id})" v-hasPermi="['sysMessage:view']">查看</el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'修改消息信息',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['sysMessage:edit']">修改</el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['sysMessage:delete']">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页区域 -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import sysMessageForm from './messageForm'
  export default {
    components: {sysMessageForm},
    data() {
      return {
        loading: true,//是否加载完成
        selectedData: [],//勾选数据
        total: 0, //记录数
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          msgType: "",
          pid: "",
          userId: "",
          refId: "",
          content: ""
        }
      };
    },
    mounted() {
      //初始化数据
      this.getList();
    },
    computed: {
      //【消息类型】
      msgTypeDict() {
        return this.$store.getters.getDictArray('msg_type');
      },
    },
    methods: {
      //点击搜索
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //查询列表数据
      getList() {
        this.$request.doGet('/admin/sysMessage/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        });
      },
      //重置查询
      resetQuery() {
        this.$refs['queryForm'].resetFields();
      },
      //打开弹窗
      openDialog(option) {
        this.$refs.sysMessageDialog.open(option);
      },
      //选择页数
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //选择页码
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //获取勾选数据
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //勾选批量删除
      handleDeleteAll() {
        const that = this;
        that.showConfirm('确定删除选中的除数据吗?', function () {
          if (that.selectedData.length > 0) {
            that.selectedData.forEach(function (item, index) {
              that.delete(item.id);
            });
            that.showSuccess("删除成功");
          } else {
            that.showWarn("至少选择一条数据！");
          }
        });
      },
      //选中行删除
      handleDelete(id) {
        const that = this;
        that.showConfirm('确定删除选中的除数据吗?', function () {
          that.delete(id);
          that.showSuccess("删除成功");
        });
      },
      //删除方法
      delete(id) {
        this.$request.doGet('/admin/sysMessage/delete?id=' + id).then(res => {
          if (res.isOk) {
            this.getList();
          }
        });
      },
    }
  };
</script>



