<template>
  <div class="service">
    <div class="service-body">
        <el-row style="height: 500px">
          <el-col :span="4">
            <div style="margin: 10px;padding:20px;background: white">
              <div style="margin-bottom: 20px">
                <div class="title" style="padding: 10px;font-size: medium;font-weight: bold">用户信息</div>
                <div class="title" @click="active='baseInfo'" style="padding:8px 20px;font-size: small">基础信息</div>
                <div class="title" @click="active='address'" style="padding:8px 20px;font-size: small">收货地址</div>
              </div>
              <div style="margin-bottom: 20px">
                <div class="title" style="padding: 10px;font-size: medium;font-weight: bold">订单中心</div>
                <div class="title" style="padding:8px 20px;font-size: small">购物车</div>
                <div class="title" @click="active='ordersList'" style="padding:8px 20px;font-size: small">我的订单</div>
                <div class="title" style="padding:8px 20px;font-size: small">我的收藏</div>
              </div>
            </div>
          </el-col>
          <el-col :span="20">
            <div style="margin: 10px;padding:30px;background: white" v-if="userInfo.id">
              <user-base-info v-if="active=='baseInfo'"></user-base-info>
              <user-address v-else-if="active=='address'"></user-address>
              <ordersList v-else-if="active=='ordersList'"></ordersList>
            </div>
            <el-empty description="登录后才能查看" v-else>
              <user-login ref="userLogin"></user-login>
              <el-button type="primary" size="mini" plain @click="login">去登录</el-button>
            </el-empty>
          </el-col>
        </el-row>
    </div>
  </div>
</template>
<script>
  import userLogin from "./../../home/personal/userLogin";
  import userBaseInfo from "./user-baseInfo"
  import userAddress from "./user-address";
  import ordersList from "./../orders/ordersList";
  export default {
    components:{userLogin,userBaseInfo, userAddress,ordersList},
    data() {
      return {
        active:"baseInfo"
      };
    },
    mounted() {
    },
    computed: {
      userInfo(){
        return this.$store.getters.userInfo
      }
    },
    methods: {
      //查询列表数据
      getList() {
        this.$request.doGet('/api/goods/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        });
      },
      login() {
        this.$refs.userLogin.open({title:"用户登录"})
      },
    }
  };
</script>
<style scoped>
  .title:hover{
    color: #ff6700;
    cursor:pointer;
    text-shadow: 1px 1px 3px rgba(43, 74, 120, 0.27);
  }
  .service{
    height: calc(100vh - 230px);
    overflow-y: auto;
    background: #efefef;
    color: #333
  }
  .service-body{
    width:1200px;margin: 0 auto
  }

</style>





