<!-- 【支付中心】弹窗页面 -->
<template>
  <div class="body">
    <el-dialog width="500px" style="margin-top: 150px" :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form style="height: 300px;" :model="orders" ref="noticeForm" :rules="rules" label-width="100px" class="edit-form">
<!--          <div v-for="orders in ordersList">-->
<!--            <el-tag size="small">订单号:</el-tag><el-tag size="small">{{orders.id}}</el-tag>-->
<!--            <el-tag size="small">价格:</el-tag><el-tag size="small">{{orders.price}}</el-tag>-->
<!--          </div>-->
          <el-input type="text" v-model="userInfo.userName" placeholder="请输入联系人"/>
          <el-input type="text" v-model="userInfo.phone" placeholder="请输入手机号"/>
          <el-input type="textarea" v-model="userInfo.address" placeholder="请输入收货地址"/>
        <div style="font-size: medium;padding-top: 10px"><span>支付金额:</span><span style="color: red">{{totalPrice}}元</span></div>
          <el-radio-group v-model="payType" style="margin-top: 10px">
            <el-radio :label="1">微信支付<el-image style="width: 50px;height: 50px;vertical-align:middle;" src="/static/images/client/pay/weixin.png"/></el-radio>
            <el-radio :label="2">支付宝<el-image style="width: 50px;height: 50px;vertical-align:middle;" src="/static/images/client/pay/alipay.png"/></el-radio>
            <el-radio :label="3">云闪付<el-image style="width: 50px;height: 50px;vertical-align:middle;" src="/static/images/client/pay/yunshan.png"/></el-radio>
          </el-radio-group>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel">我再想想</el-button>
        <el-button size="small" type="danger" @click="subPay()">确认支付</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "noticeForm",
    data() {
      return {
        payType: 2,
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        ordersList:[],
        //表单数据
        orders: {
          id: "",
          picture: "",
          title: "",
          name: "",
          orderTime: "",
          goodsId: "",
          color: "",
          price: "",
          amount: "",
          specs: "",
          orderStatus: "",
          remarks: ""
        },
      }
    },
    computed:{
      userInfo(){
        return this.$store.getters.userInfo;
      },
      totalPrice(){
        let totalPrice = 0;
        if(this.ordersList.length>0){
          for (let i = 0; i < this.ordersList.length; i++) {
            //总价累加
            let price = parseFloat(this.ordersList[i].price).toFixed(2);
            let amount = parseInt(this.ordersList[i].amount);
            totalPrice += price * amount;
          }
        }
        return totalPrice
      },
    },
    methods: {
      //打开弹窗
      open(option) {
        if(option.type=='cart'){//购物车结算
          this.ordersList=option.ordersList;
        }else{//直接购买
          this.ordersList = new Array(option.orders);
        }
        this.showDialog(option.title, false);
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false;
      },
      //提交表单
      subPay: function () {
        if (this.userInfo.userName == '') {
          this.showWarn("请填写联系人")
          return
        }
        if (this.userInfo.phone == '') {
          this.showWarn("请填写手机号")
          return
        }
        if (this.userInfo.address == '') {
          this.showWarn("请填写收货地址")
          return
        }
        const params = {
          userPhone: this.userInfo.phone,
          userName: this.userInfo.userName,
          userAddr: this.userInfo.address,
          list: this.ordersList
        };
        this.$request.doPost('/api/orders/payOrder',params).then(res => {
          if (res.isOk) {
            this.cancel();
            this.$emit("reloadList")
            this.showSuccess("支付成功");
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.noticeForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>

</style>



