<!-- 【订单信息】管理页面 -->
<template>
  <div class="service">
    <div class="service-body">
      <div class="page-shopping-cart" style="background: white" id="shopping-cart">
        <h4 class="cart-title">购物清单</h4>
        <div class="cart-product clearfix">
          <table>
            <thead>
            <tr class="cart-product-title">
              <th class="td-check">
                <input type="checkbox" class="check-span fl check-all"  :class="{'check-true':isSelectAll}" @click="selectProduct(isSelectAll)" :checked="isSelectAll" id="checkAll"></input>全选</th>
              <th class="td-product">商品</th>
              <th class="td-num">数量</th>
              <th class="td-price">单价(元)</th>
              <th class="td-total">金额(元)</th>
              <th class="td-do">操作</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item,index) in productList" :key="index">
              <td class="td-check"><input type="checkbox" class="check-span"  @click="item.select=!item.select" :class="{'check-true':item.select}" :checked="item.select"></input></td>
              <td class="td-product"><img :src="item.pro_img" width="98" height="98">
                <div class="product-info">
                  <h3>{{item.pro_name}}</h3>
                  <p>品牌：{{item.pro_brand}}&nbsp;&nbsp;产地：{{item.pro_place}}</p>
                  <p>规格/纯度:{{item.pro_purity}}&nbsp;&nbsp;起定量：{{item.pro_min}}</p>
                  <p>配送仓储：{{item.pro_depot}}</p>
                </div>
                <div class="clearfix"></div>
              </td>
              <td class="td-num">
                <div class="product-num">
                  <a href="javascript:;" class="num-reduce num-do fl el-icon-minus" @click="minus(index)"><span></span></a>
                  <input type="text" class="num-input" v-model="item.pro_num" @input="search($event,index)" @blur="check($event,index)">
                  <a href="javascript:;" class=" el-icon-plus num-do fr el-icon-plus" @click="add(index)"><span></span></a>
                </div>
              </td>
              <td class="td-price">
                <p class="red-text">￥<span class="price-text">{{item.price.toFixed(2)}}</span></p>
              </td>
              <td class="td-total">
                <p class="red-text">￥<span class="total-text">{{(item.price*item.pro_num).toFixed(2)}}</span></p>
              </td>
              <td class="td-do"><a href="javascript:;" class="product-delect "  @click="deleteOneProduct(index)">删除</a></td>
            </tr>
            </tbody></table>
        </div>
        <div class="cart-product-info">
          <a class="delect-product" href="javascript:;" @click="deleteProduct">删除所选商品</a>
          <a class="keep-shopping" href="#">继续购物</a>
          <a class="btn-buy fr" href="javascript:;">去结算</a>
          <p class="fr product-total">￥<span>{{getTotal.totalPrice}}</span></p>
          <p class="fr check-num"><span>{{getTotal.totalNum}}</span>件商品总计（不含运费）：</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import ordersForm from './ordersDetail'
    export default {
      components: {ordersForm},
      data() {
        return {
          productList:[]
        };
      },
      mounted:function () {
        //为productList添加select（是否选中）字段，初始值为true
        var _this=this;
        //为productList添加select（是否选中）字段，初始值为true
        this.productList.map(function (item) {
          _this.$set(item, 'select', true);
        })
      },
      computed: {
        isSelectAll:function(){
          //如果productList中每一条数据的select都为true，返回true，否则返回false;
          return this.productList.every(function (val) { return val.select});
        },
        getTotal:function(){
          //获取productList中select为true的数据。
          var _proList=this.productList.filter(function (val) { return val.select}),totalPrice=0;
          for(var i=0,len=_proList.length;i<len;i++){
            //总价累加
            totalPrice+=_proList[i].pro_num*_proList[i].price;
          }
          //选择产品的件数就是_proList.length，总价就是totalPrice
          return {totalNum:_proList.length,totalPrice:totalPrice}
        }
      },
      methods:{
        selectProduct:function(_isSelect){
          //遍历productList，全部取反
          for (var i = 0, len = this.productList.length; i < len; i++) {
            this.productList[i].select = !_isSelect;
          }
        },//删除已经选中(select=true)的产品
        deleteProduct:function () {
          this.productList=this.productList.filter(function (item) {return !item.select})
        },
        //删除单条产品
        deleteOneProduct:function (index) {
          //根据索引删除productList的记录
          this.productList.splice(index,1);
        },
        search: function (event,index) {
          let flag = new RegExp("^[1-9]([0-9])*$").test(event.target.value);
          if(!flag){
            this.productList[index].pro_num = '';
          }
        },
        check: function(event,index){
          if(event.target.value==''){
            this.productList[index].pro_num = 0;
          }
        },
        //数量增加
        add: function(index){
          this.productList[index].pro_num++;
        },
        //数量减少
        minus: function(index){
          if(this.productList[index].pro_num>0){
            this.productList[index].pro_num--;
          }else{
            this.productList[index].pro_num = 0;
          }

        }
      }

    };
</script>

<style scoped>
  .page-shopping-cart {
    width: 1200px;
    margin: 50px auto;
    font-size: 14px;
    border: 1px solid #e3e3e3;
  }

  .page-shopping-cart .cart-title {
    color: #317ee7;
    font-size: 16px;
    text-align: left;
    padding-left: 20px;
    line-height: 68px;
  }

  .page-shopping-cart .red-text {
    color: #e94826;
  }

  .page-shopping-cart .check-span {
    display: block;
    width: 24px;
    height: 20px;
  }

  .page-shopping-cart .td-check {
    width: 80px;
  }

  .page-shopping-cart .td-product {
    width: 460px;
  }

  .page-shopping-cart .td-num, .page-shopping-cart .td-price, .page-shopping-cart .td-total {
    width: 160px;
  }

  .page-shopping-cart .td-do {
    width: 148px;
  }

  .page-shopping-cart .cart-product-title {
    text-align: center;
    height: 38px;
    line-height: 38px;
    background: #f7f7f7;
  }

  .page-shopping-cart .cart-product-title .td-product {
    text-align: center;
    font-size: 14px;
  }

  .page-shopping-cart .cart-product-title .td-check {
    text-align: left;
  }

  .page-shopping-cart .cart-product-title .td-check .check-span {
    margin: 9px 6px 0 0;
  }

  .page-shopping-cart .cart-product {
    text-align: center;
  }

  .page-shopping-cart .cart-product table {
    width: 100%;
    text-align: center;
    font-size: 14px;
  }

  .page-shopping-cart .cart-product table th {
    padding: 10px;
  }

  .page-shopping-cart .cart-product table td {
    padding: 20px 10px;
  }

  .page-shopping-cart .cart-product table tr {
    border-bottom: 1px dashed #e3e3e3;
  }

  .page-shopping-cart .cart-product table tr:last-child {
    border-bottom: none;
  }

  .page-shopping-cart .cart-product table .product-num {
    border: 1px solid #e3e3e3;
    display: inline-block;
    text-align: center;
  }

  .page-shopping-cart .cart-product table .product-num .num-do {
    padding-top: 8px;
    width: 24px;
    height: 28px;
    display: block;
    background: #f7f7f7;
  }

  .page-shopping-cart .cart-product table .product-num .num-reduce span {
    /*background: url('../images/minus.png') no-repeat center;*/
    display: block;
    width: 24px;
    height: 28px;
  }

  .page-shopping-cart .cart-product table .product-num .num-add span {
    /*background: url('../images/add.png') no-repeat center;*/
    display: block;
    width: 24px;
    height: 28px;
  }

  .page-shopping-cart .cart-product table .product-num .num-input {
    width: 42px;
    height: 28px;
    line-height: 28px;
    border: none;
    text-align: center;
  }

  .page-shopping-cart .cart-product table .td-product {
    text-align: left;
    font-size: 12px;
    line-height: 20px;
  }

  .page-shopping-cart .cart-product table .td-product img {
    border: 1px solid #e3e3e3;
    margin-right: 10px;
  }

  .page-shopping-cart .cart-product table .td-product .product-info {
    display: inline-block;
    vertical-align: middle;
  }

  .page-shopping-cart .cart-product table .td-do {
    font-size: 12px;
  }

  .page-shopping-cart .cart-product-info {
    height: 50px;
    line-height: 50px;
    background: #f7f7f7;
    padding-left: 20px;
  }

  .page-shopping-cart .cart-product-info .delect-product {
    color: #666;
  }

  .page-shopping-cart .cart-product-info .product-total {
    font-size: 14px;
    color: #e94826;
  }

  .page-shopping-cart .cart-product-info .product-total span {
    font-size: 20px;
  }

  .page-shopping-cart .cart-product-info .check-num {
    color: #333;
  }

  .page-shopping-cart .cart-product-info .check-num span {
    color: #e94826;
  }

  .page-shopping-cart .cart-product-info .keep-shopping {
    color: #666;
    margin-left: 40px;
  }

  .page-shopping-cart .cart-product-info .btn-buy {
    height: 50px;
    color: #fff;
    font-size: 20px;
    display: block;
    width: 110px;
    background: #ff7700;
    text-align: center;
    margin-left: 30px;
  }

  .page-shopping-cart .cart-worder {
    padding: 20px;
  }

  .page-shopping-cart .cart-worder .choose-worder {
    color: #fff;
    display: block;
    background: #39e;
    width: 140px;
    height: 40px;
    line-height: 40px;
    border-radius: 4px;
    text-align: center;
    margin-right: 20px;
  }

  .page-shopping-cart .cart-worder .worker-info {
    color: #666;
  }

  .page-shopping-cart .cart-worder .worker-info img {
    border-radius: 100%;
    margin-right: 10px;
  }

  .page-shopping-cart .cart-worder .worker-info span {
    color: #000;
  }
</style>



