<!-- 【通知信息】弹窗页面 -->
<template>
  <div class="service">
    <div class="service-body">
      <!--弹窗详情-->
      <goods-detail ref="goodsDetail"></goods-detail>
      <!--轮播区域-->
      <sui-notice></sui-notice>
      <span @click="setType()" class="goodsType">全部</span>
      <span class="goodsType" @click="setType(type.dictValue)" v-for="type in typeDict">{{type.dictLabel}}</span>
      <el-row style="margin:30px;text-align: center;clear: both">
        <el-input v-model="queryForm.title" placeholder="请输入查询信息" clearable style="width: 300px;"></el-input>
        <el-button icon="el-icon-search" circle @click="getList()"></el-button>
      </el-row>
      <div v-for="type in typeDict" style="clear: both;">
        <div v-if="getTypeList(type.dictValue).length>0">
          <div style="margin-left: 10px;font-size: larger;padding-top: 20px">
            <span style="float: left">{{type.dictLabel}}</span>
            <div style="clear: both"></div>
          </div>
          <div class="card" v-for="goods in getTypeList(type.dictValue)" @click="handleView(goods.id)">
            <img :src="formatUrl(goods.picture)" class="image">
            <span style="font-size: smaller;display: block;padding: 5px">{{goods.title}}</span>
            <span style="font-size: small;display: block;padding: 5px;color: #b0b0b0">{{goods.title}}</span>
            <span style="font-size: smaller;display: block;padding: 5px;color: red">{{goods.price}}</span>
          </div>
        </div>
      </div>
      <el-empty v-if="dataList.length==0"></el-empty>
    </div>
  </div>
</template>
<script>
  import goodsDetail from './goodsDetail'
  export default {
    components: {goodsDetail},
    data() {
      return {
        loading: true,//是否加载完成
        selectedData: [],//勾选数据
        total: 0, //记录数
        dataList: [],
        typeList: [],
        queryForm: {
          current: 1,
          size: 10,
          id: "",
          picture: "",
          title: "",
          name: "",
          type: "",
          price: "",
          color: "",
          specs: "",
          remarks: ""
        }
      };
    },
    mounted() {
      //初始化数据
      this.getList();
    },
    computed: {
      //【类型】
      typeDict() {
        return this.$store.getters.getDictArray('goods_type');
      },
    },
    methods: {
      getList() {
        this.$request.doGet('/api/goods/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
          }
        });
      },
      getTypeList(type){
       return this.dataList.filter(function (item) {
           return item.type == type
        });
      },
      setType(type) {
        this.queryForm.type = type;
        this.getList()
      },
      handleView(id) {
        this.$refs.goodsDetail.open({title:'查看商品',pageFrom:'view',id:id});
      }
    }
  };
</script>
<style scoped>
  .service{
    height: calc(100vh - 230px);
    overflow-y: auto;
    background: #efefef;
    color: #333
  }
  .service-body{
    width:1200px;margin: 0 auto
  }

  .card:hover{
    padding: 11px;
    box-shadow: 5px 2px 10px rgba(43, 74, 120, 0.17)
  }
  .card {
    background: #ff6700;
    width: 280px;
    height: 320px;
    padding: 10px;
    margin: 10px;
    background: white;
    float: left;
    text-align: center
  }

  .project-name{
    float: left;
    font-size: 30px;
    text-shadow: 1px 3px 1px #ffc3a0;
    color: white;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
    margin-left: 30px;
    margin-top: 30px;
    /*margin: 0 auto; !*水平居中*!*/
  }

  .el-card{
    width: 300px;
  }

  .el-tabs--top .el-tabs__item.is-top:nth-child(2) {
    padding-left: 20px;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 5px;
    float: right;
  }

  .image {
    height: 180px;
    width: 180px;
  }

  .goodsType {
    float: left;margin: 20px;
  }
  .goodsType:hover {
    color: #ff6700;
  }
</style>





